}

.btn {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 900;
  border-width: 2px;
}
.btn:hover, .btn:active, .btn:focus {
  outline: none;
  box-shadow: none !important;
}
.btn.btn-primary:hover {
  border-color: #fff;
  background: transparent;
  color: #fff;
}
.btn.btn-black {
  border-width: 2px;
  border-color: #000;
  background: #000;
  color: #fff;
}
.btn.btn-black:hover {
